}

#zoom footer nav #check {
  visibility: hidden;
  z-index: -1;
  position: absolute;
  left: 0;
}

#zoom footer nav .first-icon, #zoom footer nav .second-icon {
  display: none;
  position: absolute;
  top: 0;
  left: 2%;
}

#zoom footer nav div {
  height: 100%;
}

#zoom footer nav .nav {
  width: 60%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#zoom footer nav .nav .first-nav {
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
}

#zoom footer nav .nav .first-nav div {
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

#zoom footer nav .nav .first-nav div:hover {
  background-color: #554f4f;
  border-radius: 5px;
}

#zoom footer nav .nav .first-nav div:nth-child(1) i {
  color: red;
}

#zoom footer nav .nav .second-nav {
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
}

#zoom footer nav .nav .second-nav div {
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 10px;
  display: flex;
}

#zoom footer nav .nav .second-nav div:hover {
  background-color: #554f4f;
  border-radius: 5px;
}

#zoom footer nav button {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px 20px;
}

@media screen and (max-width: 1086px) {
  #zoom footer {
    width: 100%;
    position: fixed;
    bottom: 0;
  }

  #zoom footer nav {
    padding: 0 10px;
  }

  #zoom footer nav .first-icon {
    height: 40px;
    cursor: pointer;
    padding: 10px 20px;
    font-size: 40px;
    display: inline-block;
  }

  #zoom footer nav .second-icon {
    height: 40px;
    cursor: pointer;
    padding: 10px 20px;
    font-size: 40px;
    display: none;
  }

  #zoom footer nav #check:checked + .first-icon {
    display: none;
  }

  #zoom footer nav #check:checked ~ .second-icon {
    display: block;
  }

  #zoom footer nav #check:checked ~ .nav .first-nav, #zoom footer nav #check:checked ~ .nav .second-nav {
    display: flex;
  }

  #zoom footer nav .nav {
    flex-direction: column;
    align-items: flex-end;
  }

  #zoom footer nav .nav .first-nav {
    flex-direction: column;
    padding: 5px 0;
    display: none;
    position: absolute;
    top: -14rem;
    left: 1%;
  }

  #zoom footer nav .nav .first-nav div {
    width: 100px;
  }

  #zoom footer nav .nav .second-nav {
    flex-direction: column;
    padding: 5px 0;
    display: none;
    position: absolute;
    top: -10rem;
    left: 1%;
  }

  #zoom footer nav .nav .second-nav div {
    width: 100px;
  }
}

@media screen and (max-width: 500px) {
  #zoom footer nav .nav .first-nav {
    height: -moz-fit-content;
    height: fit-content;
    background: #000;
  }

  #zoom footer nav .nav .first-nav div {
    width: 100vw;
  }

  #zoom footer nav .nav .second-nav {
    height: -moz-fit-content;
    height: fit-content;
    background: #000;
  }

  #zoom footer nav .nav .second-nav div {
    width: 100vw;
  }
}

